.home-post-container {
    display: flex;
    flex: column;
    background: #ebf4ff;
    border-radius: 20px;
    height: 6vh;
    width: 99%;
    margin: auto;
    margin-top: 1%;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.home-post-container:hover {
    background-color: #c4deff;
    width: 100%;
}

.home-post {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    width: 95%;
    height: 95%;
}

.home-post-profile-picture-container {
    width: 5%;
}

.home-post-profile-picture {
    width: 60%;
    border-radius: 100%;
}

.home-post-title {
    width: 45%;
}

.home-post-topic-item {
    width: 15%;
    text-align: center;
    border-radius: 10px;
    margin: 1%;
    height: 25px;
}

.home-post-btn-container {
    text-align: center;
    width: 10%;
}

.home-post-btn {
    background-color: #003478;
    border: #003478;
    color: white;
}

.home-post-btn:hover {
    background-color: #ebf4ff;
    border: #ebf4ff;
}

.home-post-date {
    width: 10%;
    text-align: end;
}

.home-post-topic-1 {
    background-color: #1ABC9C;
}

.home-post-topic-2 {
    background-color: #E67E22;
}

.home-post-topic-3 {
    background-color: #2ECC71;
}

.home-post-topic-4 {
    background-color: #F39C12;
}

.home-post-topic-5 {
    background-color: #9B59B6;
}

.home-post-topic-6 {
    background-color: #3498DB;
}

.home-post-topic-7 {
    background-color: #E74C3C;
}